import {_decorator, assetManager, Component, JsonAsset, loader, Size, resources} from 'cc';

import {Game_Board} from './Game_Board';

const {ccclass, property} = _decorator;

@ccclass('Main')
export class Main extends Component {
  @property(Game_Board) game_board: Game_Board;

  start() {
    this.game_board.show_grids(new Size(2, 2));
    resources.load(
        '../data/level_01',
        (err: Error, asset: JsonAsset) => {console.log(asset.json)});
  }
}
